:root{
    --background:#ffffff;
    --text-primary:#000;
    --text-secondary:#8e05c2;
    --accent:#8e05c2;
    --border:#333;
    --form-bg:#fff;
}
[data-theme='dark']{
    --background:#000;
    --text-primary:#fff;
    --text-secondary:#8e05c2;
    --accent:#8e05c2;
    --border:#808080;
    --form-bg:#191919;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
a{
    color: #8e05c2;
    color: var(--text-secondary);
}
.log{
    background-color: #ffffff;
    background-color: var(--background);
    color: #000;
    color: var(--text-primary);
     height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: .8rem;
    transition: all .5s;
   
}
.log-action{
    background-color: #fff;
    background-color: var(--form-bg);
    margin-left: -14%;
}
.merhaba{
    text-align: center;
    /* margin: -6rem 0 2rem; */
    font-size: 48px;
    margin-top: 10%;

}
.girisYap{
    text-align: center;
    font-size: 12px;
    padding: 1rem;
}

.theme-toggle i{
    font-size:2rem;
    cursor: pointer;
